import { Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';
import { catchError, map, tap, pluck } from 'rxjs/operators';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';

import { Friend } from './friend';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

@Injectable({
  providedIn: 'root'
})
export class InfoService {

  private friendsUrl = 'https://api.vk.com/method/friends.get?&count=5';
  private TOKEN = '';
  private USER_ID = '';

  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      console.log(error);
      console.log(`${operation} failed: ${error.message}`);
      return of(result as T);
    }
  }

  getParam(paramName = 'access_token') {
    const url = window.location.href;
    let paramValue;
    if (url.includes('#')) {
      const httpParams = new HttpParams({ fromString: url.split('#')[1] });
      paramValue = httpParams.get(paramName);
    }
    return paramValue;
  }

  getFriends(): Observable<Friend[]> {
    this.TOKEN = this.getParam('access_token');
    this.USER_ID = this.getParam('user_id');
    const url = this.friendsUrl + `&user_id=${this.USER_ID}&access_token=${this.TOKEN}&v=5.101`;
    return this.http.get<{ response: Friend[] }>(url)
      .pipe(
        map(result => result.response),
        tap(_ => console.log('fetched heroes')),
        catchError(this.handleError<Friend[]>('getHeroes', []))
      );
  }

  constructor(
    private http: HttpClient
  ) {

  }

}
